module.exports = mongoose => {
  var schema = mongoose.Schema({
      firstName: {
          type: String,
          required: true,
          default: ''
      },
      lastName: {
          type: String,
          default: '',
      },
      userRole: {
        type: String
      },
      email: {
          type: String,
          required: true,
          // unique: true,
      },
      phoneNumber: {
          type: String,
          required: true,
          default: ''
      },
      title: {
        type: String,
        required: true,
        default: ''
      },
      birthday: {
        type: String,
        required: true,
      },
      socialSecurityNumber: {
        type: String,
        required: true,
        default: ''
      },
      verifiedSocialSecurityNumber: {
        type: String,
        required: true,
        default: ''
      },
      address: {
        streetAddress: {
          type: String,
          required: true,
          default: ''
        },
        streetAddress2: {
          type: String,
          default: ''
        },
        city: {
          type: String,
          required: true,
          default: ''
        },
        state: {
          type: String,
          required: true,
          default: ''
        },
        zip: {
          type: String,
          required: true,
          default: ''
        }
      },
      photoImage: {
        type: {type: String, default: ''},
        content: {type: String, default: ''}
      },
      password: {
        type: String,
        required: true,
        default: ''
      },
      signature: {
        type: String,
        require: true,
        default: '',
      },
      logined: {
          type: Boolean,
          default: false,
      },
      entryDate: {
        type: String
      },
      driverLicense: {
        type: {type: String, default: ''},
        content: {type: String, default: ''}
      },
      socialCard: {
        type: {type: String, default: ''},
        content: {type: String, default: ''}
      },
      physicalExam: {
        type: {type: String, default: ''},
        content: {type: String, default: ''}
      },
      ppd: {
        type: {type: String, default: ''},
        content: {type: String, default: ''}
      },
      mmr: {
        type: {type: String, default: ''},
        content: {type: String, default: ''}
      },
      healthcareLicense: {
        type: {type: String, default: ''},
        content: {type: String, default: ''}
      },
      resume: {
        type: {type: String, default: ''},
        content: {type: String, default: ''}
      },
      covidCard: {
        type: {type: String, default: ''},
        content: {type: String, default: ''}
      },
      bls: {
        type: {type: String, default: ''},
        content: {type: String, default: ''}
      },
  });
  
  schema.method("toJSON", function() {
      const { _id, ...object } = this.toObject();
      object.id = _id;
      return object;
  });


  const Clinical = mongoose.model("Clinical", schema); // Changed model name to "Master"
  return Clinical;
};